
import Vue from "vue";
import rules from "@/services/helpers/validation-rules";

export default Vue.extend({
  name: "Allocation",

  props: {
    serviceType: {
      required: true,
      type: Number
    },
    order: {
      required: false,
      type: Object
    }
  },

  data: () => ({
    rules,
    model: {} as any,
    selects: {
      subdivisions: [] as Array<SelectComponentInterface>,
      branches: [] as Array<SelectComponentInterface>,
      handlers: [] as Array<SelectComponentInterface>
    },
    allowedRoles: [
      "admin",
      "chairman",
      "vice_president",
      "subdivision_head",
      "dispatcher"
    ] as Array<string>
  }),

  watch: {
    order: {
      immediate: true,
      deep: true,
      handler() {
        if (this.order) {
          this.loadEditData();
        }
      }
    }
  },

  computed: {
    user(): any {
      return this.$store.getters["user/info"];
    },
    canSelectHandler(): any {
      return (
        this.serviceType < 4 &&
        this.user?.roles.some((item: any) =>
          this.allowedRoles.includes(item.alias)
        )
      );
    }
  },

  async mounted() {
    await this.loadData();
  },

  methods: {
    async loadEditData(): Promise<void> {
      try {
        this.model.branch_id = this.order.branch_id;
        this.model.subdivision_id = this.order.subdivision_id;
        this.model.handler_id = this.order.handler_id;
        await this.loadData();
        await this.loadSubdivisionsByBranch();
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    async loadData(): Promise<void> {
      try {
        this.selects.branches = await this.$API.branches().getList();
        if (this.selects.branches.length === 1) {
          const [branch] = this.selects.branches;
          this.model.branch_id = branch.value;
        }
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    async loadSubdivisionsByBranch(): Promise<any> {
      try {
        this.selects.subdivisions = await this.$API
          .subdivisions()
          .getList({ filter: { branches: this.model.branch_id } });

        if (
          !this.selects.subdivisions.some(
            (el: any) => el.value === this.model.subdivision_id
          )
        ) {
          this.model.subdivision_id = null;
        }

        if (this.selects.subdivisions.length === 1) {
          const [subdivision] = this.selects.subdivisions;
          this.model.subdivision_id = subdivision.value;
        }

        await this.loadSpecialists();
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    async loadSpecialists(): Promise<void> {
      try {
        this.selects.handlers = await this.$API.users().getList({
          filter: {
            service_type: this.serviceType,
            branch: this.model.branch_id,
            subdivision: this.model.subdivision_id
          }
        });

        if (this.selects.handlers.length === 1) {
          const [manager] = this.selects.handlers;
          this.model.handler_id = manager.value;
        }

        if (
          !this.selects.handlers.some(
            (item: any) => item.value === this.model.handler_id
          )
        ) {
          this.model.handler_id = null;
        }
        this.setAllocations();
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    setAllocations(): void {
      this.$emit("input", this.model);
    }
  }
});
